process.on('message', function (message) {
  const axios = require("axios").default;
  const fs = require("fs");
  // fs.writeFile('F:\\\\123.txt', JSON.stringify(message) + '\r\n', {
  //   'flag': 'a'
  // })
  var FormData = require("form-data");
  var form = new FormData();

  //   fs.writeFile('F:\\\\123.txt', JSON.stringify(message.params) + '\r\n', {
  //     'flag': 'a'
  //   })

  Object.keys(message.params).forEach(key => {
    form.append(key, message.params[key]);
  })
  //   fs.writeFile('F:\\\\123.txt', JSON.stringify(message.filePath) + '\r\n', {
  //     'flag': 'a'
  //   })
  form.append("file", fs.createReadStream(message.filePath));

  let headers = {
    "User-Agent": 'Mozilla/5.0 (Linux; Android 6.0; Nexus 5 Build/MRA58N) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/83.0.4103.61 Mobile Safari/537.36',
    'Sec-Fetch-Mode': 'cors',
    'Sec-Fetch-Site': 'cross-site',
    "sec-fetch-dest": "empty",
  }

  form.getLength(async function (err, length) {
    if (err) {
      return;
    }

    //important!!!
    headers['Content-Length'] = length;
    headers['Content-Type'] = `multipart/form-data; boundary=${form.getBoundary()}`,

      axios.post(
        message.postUrl,
        form, {
          headers,
          maxContentLength: Infinity
        }
      ).then((v) => {
        process.send(true)
        // fs.writeFile('F:\\\\123.txt', JSON.stringify(v.data) + ':v.data\r\n', {
        //   'flag': 'a'
        // })
      }).catch((error) => {
        process.send(false)
        // fs.writeFile('F:\\\\123.txt', JSON.stringify(error) + ':error\r\n', {
        //   'flag': 'a'
        // })
      });



  });
})
